import React from "react";
import TodoForm from './form';

function App() {
  const [todos, setTodos] = React.useState([
    {
      text: 'learn react',
      isCompleted: false,
    },
    {
      text: 'meet friend for lunch',
      isCompleted: false,
    },
    {
      text: 'build todo app',
      isCompleted: false,
    }
  ]);

  const addTodo = text => {
    setTodos([...todos, {text, isCompleted:false}]);
  }
  const removeTodo = e => {
    let todoId = Number(e.target.id);
    setTodos(todos.filter((item, idx) => idx !== todoId))
  }

  return(
      <div className="app">
        <div className="todo-list">
          {todos.map((todo, idx) => (
              <div className="todo" key={idx} id={idx} onClick={removeTodo}>{todo.text}</div>
          ))}
          <TodoForm addTodo={addTodo} />
        </div>
      </div>
  );
}

export default App;
